.wrap{
  width: 600px;
  height: 400px;
  margin: 0 auto;
  margin-top: 50px;
}

.switch{
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.switch .btn{
  width: 20%;
  height: 20px;
  background: lightgreen;
  position: absolute;
  bottom: 0;
  border-right: 1px solid #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  opacity: 0.5;
};

.switch .btn{
  z-index: 50;
}

.btn-1{
  left: 0;
}

.btn-2{
  left: 20%;
}

.btn-3{
  left: 40%;
}

.btn-4{
  left: 60%;
}

.btn-5{
  left: 80%;
}

.content{
  width: 100%;
  height: 100%;
  background: lightblue;
  position: relative;
  z-index: -1;
  left: 0;
  top: 0;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  -webkit-animation: translate 20s linear 0s infinite;
          animation: translate 20s linear 0s infinite;
}

@-webkit-keyframes translate {
  0%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  1%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  19%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  20%{
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
  39%{
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
  40%{
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
  }
  59%{
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
  }
  60%{
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
  }
  79%{
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
  }
  80%{
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }
  99%{
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }
  100%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@keyframes translate {
  0%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  1%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  19%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  20%{
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
  39%{
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
  40%{
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
  }
  59%{
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
  }
  60%{
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
  }
  79%{
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
  }
  80%{
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }
  99%{
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }
  100%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

.content img{
  width: 100%;
  height: 100%;
  position: absolute;
}

.img-1{
  top: 0;
  left: 0;
}

.img-2{
  top: 0;
  left: -100%;
}

.img-3{
  top: 0;
  left: 100%;
}

.img-4{
  top: 100%;
  left: 0;
}

.img-5{
  top: -100%;
  left: 0;
}

.switch .top{
  width: 20%;
  height: 20px;
  background: #e84848;
  z-index: 120;
  position: absolute;
  bottom: 0;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-animation: top-translate 20s ease-in-out 0s infinite;
          animation: top-translate 20s ease-in-out 0s infinite;
  border-right: 1px solid #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  opacity: 0.8;
}

@-webkit-keyframes top-translate{
  0%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  1%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  19%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  20%{
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
  39%{
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
  40%{
    -webkit-transform: translate3d(200%, 0, 0);
            transform: translate3d(200%, 0, 0);
  }
  59%{
    -webkit-transform: translate3d(200%, 0, 0);
            transform: translate3d(200%, 0, 0);
  }
  60%{
    -webkit-transform: translate3d(300%, 0, 0);
            transform: translate3d(300%, 0, 0);
  }
  79%{
    -webkit-transform: translate3d(300%, 0, 0);
            transform: translate3d(300%, 0, 0);
  }
  80%{
    -webkit-transform: translate3d(400%, 0, 0);
            transform: translate3d(400%, 0, 0);
  }
  99%{
    -webkit-transform: translate3d(400%, 0, 0);
            transform: translate3d(400%, 0, 0);
  }
  100%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@keyframes top-translate{
  0%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  1%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  19%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  20%{
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
  39%{
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
  40%{
    -webkit-transform: translate3d(200%, 0, 0);
            transform: translate3d(200%, 0, 0);
  }
  59%{
    -webkit-transform: translate3d(200%, 0, 0);
            transform: translate3d(200%, 0, 0);
  }
  60%{
    -webkit-transform: translate3d(300%, 0, 0);
            transform: translate3d(300%, 0, 0);
  }
  79%{
    -webkit-transform: translate3d(300%, 0, 0);
            transform: translate3d(300%, 0, 0);
  }
  80%{
    -webkit-transform: translate3d(400%, 0, 0);
            transform: translate3d(400%, 0, 0);
  }
  99%{
    -webkit-transform: translate3d(400%, 0, 0);
            transform: translate3d(400%, 0, 0);
  }
  100%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}




